*, :after, :before {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

p, h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

a {
  color: inherit;
  cursor: pointer;
  text-decoration: none;
}

button {
  cursor: pointer;
  border: none;
}

input {
  outline: none;
}

img {
  max-width: 100%;
  margin: 0;
  display: block;
}

ul li {
  margin: 0;
  padding: 0;
  list-style: none;
}

html, body {
  scroll-behavior: smooth;
  background-color: #fff;
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
  font-weight: 500;
  line-height: 1;
}

.stop-scroll {
  overflow: hidden;
}

.visually-hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  -webkit-clip-path: inset(50%);
  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.backdrop {
  opacity: 1;
  background-color: #0003;
  transition: opacity .3s, visibility .3s;
  position: fixed;
  inset: 0;
}

@media screen and (max-width: 767px) {
  .backdrop {
    padding: 93px 15px 15px;
  }
}

body.no_scroll {
  height: 100%;
  margin: 0;
  overflow-y: hidden;
}

.auth-modal {
  opacity: 1;
  background-color: #d3d3d3;
  border-radius: 4px;
  padding: 40px;
  transition: transform .9s;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%)scale(1)rotate(0);
  box-shadow: 0 1px 3px #0000001f, 0 1px 1px #00000024, 0 2px 1px #0003;
}

@media screen and (min-width: 320px) and (max-width: 767px) {
  .auth-modal {
    width: 90%;
    padding: 7px;
  }
}

.form__group {
  flex-direction: column;
  display: flex;
}

.form__close {
  cursor: pointer;
  width: 30px;
  height: 30px;
  filter: drop-shadow(0 4px 4px #00000040);
  z-index: 1;
  background-color: #0000;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  margin-top: 8px;
  margin-right: 8px;
  padding: 0;
  transition-property: fill;
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
  display: flex;
  position: absolute;
  top: 0;
  right: 0;
}

.form__close:hover, .form__close:focus {
  fill: #ff6b01;
}

.form__header {
  text-align: center;
  color: #000;
  margin-bottom: 12px;
  font-size: 18px;
  display: inline-block;
}

.field__label {
  flex-direction: column;
  margin-bottom: 4px;
  display: flex;
}

.field__text, .textarea__text {
  letter-spacing: .01em;
  font-size: 15px;
  line-height: 1.16;
}

.field__input {
  width: 100%;
  height: 40px;
  border: 1px solid #21212133;
  border-radius: 4px;
  outline: none;
  margin-top: 4px;
  padding-left: 12px;
  display: flex;
}

.checkbox {
  align-items: center;
  margin: 10px auto 0;
  display: flex;
}

.checkbox__input {
  width: 20px;
  height: 20px;
  margin-right: 5px;
}

.buttons {
  border-radius: 5px;
  justify-content: space-around;
  margin-top: 10px;
  display: flex;
}

.signin__btn, .signup__btn {
  color: #fff;
  background-color: #ff6b01;
  border-radius: 5px;
  padding: 5px;
}

.google__btn {
  background-color: #0000;
  margin-top: 10px;
}

.google__btn:hover, .google__btn:focus {
  fill: #ff6b01;
}

.backdrop.visually-hidden .modal {
  transform: translate(-50%, -50%)scale(0)rotate(180deg);
}

.back_to_top {
  z-index: 1000;
  width: 40px;
  text-align: center;
  color: #fff;
  cursor: pointer;
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  background: #ff6b08;
  border-radius: 50%;
  padding: 5px;
  font-weight: bolder;
  line-height: 30px;
  transition: all .5s;
  position: fixed;
  bottom: 50px;
  right: 2%;
}

@media screen and (max-width: 767px) {
  .back_to_top {
    border-radius: 50%;
    bottom: 10%;
    right: 5%;
  }
}

@media screen and (min-width: 320px) {
  .back_to_top {
    bottom: 20%;
    right: 2%;
  }
}

.back_to_top:hover {
  background: gray;
  transform: scale(1.1);
  box-shadow: 0 8px 43px #fff;
}

.back_to_top-show {
  opacity: 1;
  visibility: visible;
  pointer-events: auto;
}

.loader {
  flex-direction: column;
  align-items: center;
  display: flex;
}

.loader__text {
  color: #3d314a;
  text-transform: uppercase;
  letter-spacing: 1px;
  z-index: 2;
  margin: 5px auto;
}

.loader__filmstrip {
  width: 56px;
  height: 70px;
  background: #3d314a;
  position: relative;
}

.loader__filmstrip:before, .loader__filmstrip:after {
  content: "";
  height: 120%;
  border-left: 5px dashed #f5f1ed;
  animation: 20ms infinite roll;
  position: absolute;
}

.loader__filmstrip:before {
  left: 5px;
}

.loader__filmstrip:after {
  right: 5px;
}

@keyframes roll {
  0% {
    top: 0;
  }

  100% {
    top: -15px;
  }
}

